                    font-weight: 600;
                    p {
                        float: left;
                    }
                    a {
                        float: right;
                        font-size: 14px;
                        color: #999999;
                        font-weight: normal;
                    }
                }
                ul {
                    li {
                        @include clearfix;
                        padding: 16px 0;
                        div {
                            float: left;
                            &.img-infos {
                                margin-right: 20px;
                                width: 88px;
                                height: 88px;
                                overflow: hidden;
                                img {
                                    height: 100%;
                                }
                            }
                            &.des-infos {
                                width: 202px;
                                height: 88px;
                                box-sizing: border-box;
                                padding-right: 6px;
                                h3 {
                                    color: #121212;
                                    font-size: 16px;
                                    line-height: 24px;
                                    max-height: 48px;
                                    @include overflow-hidden;
                                    margin-bottom: 10px;
                                }
                                p {
                                    color: #888888;
                                    font-size: 16px;
                                    line-height: 24px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &#company-body {
        background-image: url('../images/bg-about.png');
        >main {
            @include clearfix;
            #company-list {
                @include clearfix;
                li {
                    float: left;
                    a {
                        height: 160px;
                        width: auto;
                        float: left;
                        img {
                            float: left;
                            height: 100%;
                            border: 1px solid #e1e1e1;
                            box-sizing: border-box;
                            padding: 6px;
                            @include rounded-corners(10px);
                            margin-right: 20px;
                        }
                    }
                }
            }
        }
    }
    &#show-body {
        background-image: url('../images/bg-about.png');
        >main {
            @include clearfix;
            >h2 {
                color: #5fb86e;
                position: relative;
                font-size: 30px;
                border: 1px solid #5fb86e;
                p {
                    width: 200px;
                    height: 60px;
                    line-height: 60px;
                    background-color: #fff;
                    text-align: center;
                    @include positions($top: 50%, $left:50%);
                    margin-left: -100px;
                    margin-top: -30px;
                }
            }
            #show-list {
                margin-top: 30px;
                @include clearfix;
                li {
                    height: 100px;
                    line-height: 100px;
                    padding: 0 30px;
                    border-bottom: 1px solid #5fb86e;
                    h3 {
                        float: left;
                    }
                    span {
                        float: right;
                        margin-right: 60px;
                    }
                    a {
                        float: right;
                        background-color: #5fb86e;
                        height: 40px;
                        color: #fff;
                        line-height: 40px;
                        padding: 0 10px;
                        margin-top: 30px;
                        @include rounded-corners(5px);
                    }
                }
            }
            #show-pages {
                text-align: center;
                @include clearfix;
                margin-top: 20px;
                li {
                    width: 20px;
                    height: 20px;
                    background-color: #fff;
                    border: 1px solid #edba67;
                    color: #edba67;
                    @include rounded-corners(4px);
                    line-height: 20px;
                    text-align: center;
                    display: inline-block;
                    margin-right: 6px;
                    &.active {
                        color: #fff;
                        background-color: #edba67;
                        border: 1px solid #edba67;
                    }
                }
            }
        }
    }
}